*{box-sizing:border-box;margin:0;padding:0;font-family:'Roboto', sans-serif;color:#262626;overflow-x:hidden}

.category{background:#EDECEC;padding:30px 0px;display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-evenly;grid-gap:20px;gap:20px}.ventures{width:100%;background:#EDECEC;display:flex;flex-direction:column}

.navBar{width:100%;height:80px;display:flex;flex-direction:row;padding:10px 30px;position:fixed;justify-content:center;align-items:center;overflow:hidden;z-index:5;transition:all 0.2s ease-in-out}.navBar.fix{height:60px;box-shadow:0px 10px 10px rgba(156,156,156,0.2);background:#fff}.navBar .brandLogo{margin-left:2vw;transition:all 0.2s ease-in-out;cursor:pointer}.navBar .brandLogo img{width:50px;height:50px}.navBar .brandLogo:hover{transform:translateY(-5px)}.navBar .menuOptions{flex-grow:1;display:flex;flex-direction:row;grid-gap:10px;gap:10px;justify-content:flex-end;margin-right:10vw}.navBar .menuOptions .menuButton{width:100px}.navBar .hamButton{height:100%;flex-grow:1;display:flex;flex-direction:row;justify-content:flex-end;align-items:center;cursor:pointer}.navBar .hamButton img{width:30px;height:30px;transition:all 0.2s ease-in-out;overflow:hidden}.navBar .hamButton img:hover{transform:scale(1.1)}

.drawer{position:fixed;width:100%;transform:translateY(-1000px);display:flex;flex-direction:row;justify-content:space-around;align-items:center;border-bottom-left-radius:5px;border-bottom-right-radius:5px;grid-gap:10px;gap:10px;z-index:10;background:#fff;-webkit-animation:draw 2s;animation:draw 2s;transition:0.5s;overflow:hidden;padding:3vw}.drawer.active{box-shadow:0px 10px 10px rgba(156,156,156,0.2);transform:translateY(0px)}

.card{width:20vw;height:20vw;background:#fff;background-size:cover;display:flex;flex-direction:column;justify-content:flex-end;transition:all 0.2s ease-in-out;border-radius:5px;cursor:pointer}.card .cardContent{width:100%;min-height:100px;padding:10px;background:linear-gradient(to bottom, transparent, #262626);display:flex;flex-direction:column;justify-content:flex-end}.card .cardContent h3{font-size:2vw;font-family:"Poppins";color:#fff;transition:all 0.2s ease-in-out}.card:hover{transform:translateY(-10px);opacity:0.8;box-shadow:0px 10px 10px rgba(156,156,156,0.2)}.card:hover h3{font-size:2.1vw;font-family:"Poppins";color:#fff}

.homeScreen{width:100vw;height:100vh;background:url(/munnasGroup/static/media/home7.71a0dad8.jpg);background-size:cover;background-position:center;display:flex;flex-direction:column;overflow:hidden;padding-top:15vh;padding-left:10vw;padding-right:10vw;padding-bottom:10vh}.titleScreen{width:100%;height:100%;display:flex;flex-direction:column;justify-content:center;align-items:center}.titleScreen .titleBox{width:100%;height:100%;display:flex;flex-direction:column;justify-content:center;padding:5vw}@media (max-width: 720px){.titleScreen .titleBox{justify-content:center;align-items:center}}.titleScreen .titleBox h1{font-size:8rem;font-family:"Cinzel";text-shadow:0px 10px 10px rgba(156,156,156,0.2);font-weight:400;padding:5px;-webkit-animation:fadeIn 1s ease-in-out;animation:fadeIn 1s ease-in-out}@media (max-width: 720px){.titleScreen .titleBox h1{font-size:3rem}}.titleScreen .titleBox h3{font-size:5rem;font-family:"Cinzel";text-shadow:0px 10px 10px rgba(156,156,156,0.2);font-weight:400;padding:5px;-webkit-animation:fadeIn 1.5s ease-in-out;animation:fadeIn 1.5s ease-in-out}@media (max-width: 720px){.titleScreen .titleBox h3{font-size:2rem}}.titleScreen .titleBox span{font-family:"Cinzel";padding:5px}@media (max-width: 720px){.titleScreen .titleBox span{text-align:center}}@-webkit-keyframes fadeIn{0%{opacity:0;transform:translateX(50px)}100%{opacity:1;transform:translateX(0px)}}@keyframes fadeIn{0%{opacity:0;transform:translateX(50px)}100%{opacity:1;transform:translateX(0px)}}.titleScreen .toStoryButton{height:100px;width:200px;display:flex;padding-top:10px;padding-left:5px}@media (max-width: 720px){.titleScreen .toStoryButton{justify-content:center}}.titleScreen .toStoryButton div{width:120px;height:50px;justify-content:center;align-items:center;display:flex;transition:all 0.5s ease-in-out;border:1px solid #262626;cursor:pointer}.titleScreen .toStoryButton div span{font-size:1.2em;color:#262626}.titleScreen .toStoryButton div:hover{background:#262626}.titleScreen .toStoryButton div:hover span{color:#fff}

.story{width:100%;min-height:100vh;display:flex;flex-direction:row;flex-wrap:wrap;justify-content:center;align-items:center;overflow:hidden}.story .storyImg{width:50%;min-width:500px;height:100%;padding:50px 0px;display:flex;justify-content:center;align-items:center}.story .storyImg img{box-shadow:0px 10px 10px rgba(156,156,156,0.2)}@media (max-width: 720px){.story .storyImg img{width:80vw;height:80vw}}.story .storyContent{width:50%;height:100%;min-width:500px;height:100%;padding:50px 0px;display:flex;justify-content:center;align-items:center;padding:5rem}.story .storyContent .contentBox{border:2px solid #262626;padding:5rem;display:flex;grid-gap:20px;gap:20px;flex-direction:column;justify-content:center;align-items:center;box-shadow:0px 10px 10px rgba(156,156,156,0.2)}.story .storyContent .contentBox .heading h3{font-family:'Cinzel', serif;font-size:6rem;line-height:6rem;overflow:hidden;text-shadow:0px 10px 10px rgba(156,156,156,0.2)}@media (max-width: 720px){.story .storyContent .contentBox .heading h3{font-size:5rem;line-height:5rem}}.story .storyContent .contentBox span{font-family:'Prata', serif;font-weight:400;text-align:justify}

.vision{width:100%;min-height:100vh;display:flex;flex-direction:row;flex-wrap:wrap}.vision .visionLeft{padding:5vw;flex-grow:1;display:flex;flex-direction:column;justify-content:center;text-align:justify}.vision .visionLeft h1{font-family:"Cinzel";font-size:6rem;text-shadow:0px 10px 10px rgba(156,156,156,0.2)}@media (max-width: 720px){.vision .visionLeft h1{font-size:5rem}}.vision .visionLeft span{width:300px;font-weight:600}.vision .visionRight{min-width:300px;flex-grow:1;padding:4vw}.vision .visionRight .visionCard{width:100%;height:100%;min-height:300px;padding:5vw;display:flex;flex-direction:column;justify-content:center;align-items:center;border-top-right-radius:50px;border-bottom-left-radius:50px;border-bottom-right-radius:50px;background:#EDECEC}.vision .visionRight .visionCard span{font-size:5vw;font-weight:600}@media (max-width: 720px){.vision .visionRight .visionCard span{font-size:30px}}

.contactUs{width:100vw;height:100vh;padding:4vw 2vw 2vw 2vw;background:#EDECEC}.contactUs .contactBox{width:100%;height:100%;background:#fff;display:flex;flex-direction:row;justify-content:center;flex-wrap:wrap;box-shadow:0px 10px 10px rgba(156,156,156,0.2)}.contactUs .contactBox .contactDetails{width:50%;min-width:300px;display:flex;flex-direction:column;align-items:flex-start;justify-content:center;grid-gap:10px;gap:10px;padding:5vw}.contactUs .contactBox .contactDetails h3{font-family:"Cinzel";font-size:4vw;text-shadow:0px 10px 10px rgba(156,156,156,0.2);margin-bottom:10px;padding:20px}@media (max-width: 720px){.contactUs .contactBox .contactDetails h3{font-size:30px}}.contactUs .contactBox .contactDetails .contactItem{display:flex;flex-direction:row;transition:all 0.2s ease-in-out;grid-gap:10px;gap:10px;cursor:pointer}.contactUs .contactBox .contactDetails .contactItem a{text-decoration:none;display:flex;flex-direction:column}.contactUs .contactBox .contactDetails .contactItem img{width:25px;height:25px}.contactUs .contactBox .contactDetails .contactItem span{font-family:"Cinzel";text-align:justify;font-weight:400}.contactUs .contactBox .contactDetails .contactItem span a{text-decoration:none;font-family:"Cinzel"}.contactUs .contactBox .contactDetails .contactItem .address{display:flex;flex-direction:column}.contactUs .contactBox .contactDetails .contactItem:hover{transform:translateY(-5px)}.contactUs .contactBox .contactLocation{width:50%;min-width:500px;display:flex;justify-content:center;align-items:center;padding:4vw 2vw 2vw 2vw}.contactUs .contactBox .contactLocation .map{background:url(/munnasGroup/static/media/mapLocation.03824bc6.png);width:100%;height:100%;border-radius:10px;background-size:cover}@media (max-width: 720px){.contactUs .contactBox .contactLocation{min-width:300px}}

.services{width:100%;min-height:100vh;padding:4vw 2vw;display:flex;flex-direction:column;flex-wrap:wrap;grid-gap:20px;gap:20px;background:url(/munnasGroup/static/media/y2.bbf536e8.jpg);background-size:cover}.services h1{font-size:6vw}@media (max-width: 720px){.services h1{font-size:60px}}.services .servicesBox{width:100%;flex-grow:1;display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-evenly;align-items:center;grid-gap:20px;gap:20px}.services .servicesBox .serviceItem{width:500px;height:500px;min-width:300px;min-height:300px;border-radius:10px;margin:10px;display:flex;flex-direction:column;justify-content:flex-end;background:#fff;transition:all 0.2s ease-in-out;background-size:cover;cursor:pointer}.services .servicesBox .serviceItem .itemName{padding:20px;background:linear-gradient(to bottom, transparent, #262626)}.services .servicesBox .serviceItem h2{color:#fff}@media (max-width: 720px){.services .servicesBox .serviceItem{width:300px;height:400px}}.services .servicesBox .serviceItem:hover{transform:translateY(-10px)}

